import Vue from 'vue'
import Router from 'vue-router'
import openp from '@/components/openP'
import Repeat from '@/components/repeat'
import Red from '@/components/red'
import Red2 from '@/components/red2'
import receive from '@/components/receive'
import Ahoneypot from '@/components/Ahoneypot'
import Ceshi from '@/components/ceshi'
import Ceshi2 from '@/components/ceshi2'
import Hello from '@/components/Hello'
import one from '@/components/one'
import two from '@/components/two'
import five from '@/components/five'


Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		redirect: '/openp'
	}, {
		path: '/openp',

		component: openp
	}, {
		path: '/ceshi2',

		component: Ceshi2
	}, {
		path: '/repeat',
		component: Repeat
	}, {
		path: '/red',

		component: Red
	}, {
		path: '/red2',
		component: Red2
	},{
		path: '/receive',
		component: receive
	},{
		path: '/ahoneypot',
		component: Ahoneypot
	},{
		path: '/ceshi',
		component: Ceshi
	},{
		path: '/hello',
		component:Hello
	},{
		path: '/one',
		component:one
	},{
		path: '/two',
		component:two
	},{
		path: '/five',
		component:five
	}
	]
})


