<template>
  <div id="app" >
   <ul class= "app-ul">
     <li>
       <img src = "../assets/椭圆-1.png" >
       <p>A. 扣除20分信用积分</p>
     </li>
     <li>
        <img src = "../assets/椭圆-1.png" >
       <p>B. 情节严重着扣除全部押金</p>
     </li>
     <li>
        <img src = "../assets/椭圆-1.png" >
       <p>C. 以上全对</p>
     </li>
   </ul>
    <div class = "app-button">
      <div><a onclick="window.history.go(-1)"><img src = "../assets/shangH.png"></a></div>
      <div><router-link tag="a" :to="{path:'/red'}"><img src = "../assets/qinmi.png" ></router-link></div>
      
    </div>
    <div class = "xuanze" v-show="num7">
        <div @click="num7=!num7">
          <h2>抱歉!</h2>
          <p>您已经领取过该奖励,<br />统一手机号码最多只能领取2次。</p>
          <button>朕知道了</button>
        </div>
    </div>
  </div>
</template>

<script>
(function(win, doc) {
    win.onresize = function() {
        change();
    };
    change();

    function change() {
        var oFs = doc.documentElement.clientWidth / (320 / 20);
        doc.documentElement.style.fontSize = oFs + 'px';
    }
})(window, document);

export default {
    data () {
    return {
       num7:true
     
    }
  }
}
</script>

<style lang = "less" scoped>
*{
  margin:0;
  padding:0;

}
a{
	text-decoration: none;
}
#app{
  position:relative;
  width:16rem;
  height:28.4rem;
  background: url("../assets/第5页.jpg");
  background-size:16rem 28.4rem;
  .app-ul{
    width:90%;
    margin:0 auto;
    padding-top:14.5rem;

    li{
      border-top:1px solid #ccc;
      width:100%;
      height:2.25rem;
      display: flex;
      line-height: 2.25rem;
      img{
        width:0.625rem;
        height:0.625rem;
        margin:0.8rem;
      }
    }
    li:last-child{
      border-bottom:1px solid #ccc;
    }
  }
  .app-button{
    width:90%;
    margin:0 auto;
    display: flex;
    justify-content: space-between;
    margin-top:0.5rem;
    div{
      img{
        width:6.35rem;
        height:1.925rem;
      }
    }
  }
  .xuanze,.bingg{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.5);
    
  }
  .xuanze{
    
    div{
      width:13rem;
      height:9.375rem;
      margin:0 auto;
      background:url("../assets/sorry.png");
      background-size:13rem 9.375rem;
      position:absolute;
      top:9rem;
      left:0.5rem;
      position:relative;
      text-align: center;
      h2{
        font-size:1rem;
        position:absolute;
        top:4.5rem;
        left:44%;
        text-align: center;
      }
      p{
      	position: absolute;
      	top: 6rem;
      	color: #9D9D9D;
      	font-size: 0.7rem;
      	left: 1.5rem;
      }
     
    }
  }
}


    

</style>



