<template>
  <div id="app" >
   <ul class= "app-ul">
     <li>
       <a href="javascript:;" @click ="num1=true"><img src = "../assets/椭圆-1.png" class="img"></a>
       <p>A. 扣除20分信用积分</p>
     </li>
     <li>
         <a href="javascript:;" @click ="num2=true"><img src = "../assets/椭圆-1.png" class="img" ></a>
       <p>B. 情节严重着扣除全部押金</p>
     </li>
     <li>
         <a href="javascript:;" @click ="num3=true"><img src = "../assets/椭圆-1.png" class="img" ></a>
       <p>C. 以上全对</p>
     </li>
   </ul>
    <div class = "app-button">
      <div><a onclick="window.history.go(-1)"><img src="../assets/shangH.png"/></a></div>
      <div @click = "lick"><img src = "../assets/qinmi.png"></div>
      
    </div>
    <div class = "xuanze"  v-if="num6">
        <div>
          <h2>请您选择一个答案</h2>
          <button @click = "lick">朕知道了</button>
        </div>
    </div>
    <div class = "bingg" v-if="num5">
        <div>
          <p>恭喜您，答对了!</p>
          <p>将首页地图缩小显示后，将会出现蓝色覆盖区域，该区域即是小蜜蜂的运营区域。只能在运营区域范围内骑行，否则会扣除相应的押金以及信用积分。</p>
          <router-link to = "/red"><button>朕知道了</button></router-link>
        </div>
    </div>
    <div class = "sorry"  v-if="num4">
        <div>
          <p>很遗憾，正确答案是B!</p>
          <p>将首页地图缩小显示后，将会出现蓝色覆盖区域，该区域即是小蜜蜂的运营区域。只能在运营区域范围内骑行，否则会扣除相应的押金以及信用积分。</p>
          <button @click = "num4 = false">朕知道了</button>
        </div>
    </div>
  </div>
</template>

<script>

export default {
    data () {
    return {
       num1:false,
       num2:false,
       num3:false,
       num4:false,
       num5:false,
       num6:false
    }
  },
  methods:{
  	lick(){
  		this.num4=this.num1;
  		this.num5=this.num2;
  		if(this.num1 == false && this.num2==false){
  			this.num6=!this.num6
  		}
    },
    
  }
}
</script>

<style lang = "less" scoped>
*{
  margin:0;
  padding:0;

}
#app{
  position:relative;
  width:16rem;
  height:28.4rem;
  background: url("../assets/第5页.jpg");
  background-size:16rem 28.4rem;
  .app-ul{
    width:90%;
    margin:0 auto;
    padding-top:14.5rem;

    li{
      border-top:1px solid #ccc;
      width:100%;
      height:2.25rem;
      display: flex;
      line-height: 2.25rem;
      img{
        width:0.625rem;
        height:0.625rem;
        margin:0.8rem;
      }
     
    }
    li:last-child{
      border-bottom:1px solid #ccc;
    }
  }
  .app-button{
    width:90%;
    margin:0 auto;
    display: flex;
    justify-content: space-between;
    margin-top:0.5rem;
    div{
      img{
        width:6.35rem;
        height:1.925rem;
      }
    }
  }
   .act{
         width:0.625rem;
        height:0.625rem;
        margin:0.8rem;
        background:url("../assets/组-15.png");
        z-index:5;
      }
  .xuanze,.bingg,.sorry{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.5);
    
    
  }
  .xuanze{
    /* display: none; */
    div{
      width:13rem;
      height:9.375rem;
      margin:0 auto;
      background:url("../assets/选择一个答案弹窗.png");
      background-size:13rem 9.375rem;
      position:absolute;
      top:9rem;
      left:0.5rem;
      position:relative;
      h2{
        font-size:1rem;
        position:absolute;
        top:4.5rem;
        left:2rem;
      }
      button{
        width:6.35rem;
        height:1.925rem;
        background:orange;
        border:none;
        border-radius: 1rem 1rem 1rem 1rem;
         position:absolute;
        top:6.5rem;
        left:3rem;
      }
    }
  }
   .bingg{
     /* display: none; */
      div{
        width:14rem;
        height:15.75rem;
        margin:0 auto;
        background:url("../assets/答对了弹窗.png");
        background-size:14rem 15.75rem;
         text-align: centetn;
          position:absolute;
          top:8rem;
          position:relative;
         p:first-child{
           color:red;
           font-size: 1.5rem;
           position:absolute;
          top:4.5rem;
          left:1.5rem;
         }
         p:nth-of-type(2){
           width:95%;
           color:#999;
           font-size: 0.8rem;
           position:absolute;
           top:7rem;
           left:0.5rem;
         }
        
         button{
        width:6.35rem;
        height:1.925rem;
        background:orange;
        border:none;
        border-radius: 1rem 1rem 1rem 1rem;
         position:absolute;
        top:13rem;
        left:3rem;
      }
      }
   } 
   .sorry{
      div{
        width:14rem;
        height:16rem;
        margin:0 auto;
        background:url("../assets/遗憾弹窗.png");
        background-size:14rem 16rem;
         text-align: centetn;
          position:absolute;
          top:8rem;
          position:relative;
         p:first-child{
           color:red;
           font-size: 1.3rem;
           position:absolute;
          top:5.8rem;
          left:0.6rem;
         }
         p:nth-of-type(2){
           width:95%;
           color:#999;
           font-size: 0.8rem;
           position:absolute;
           top:8rem;
           left:0.4rem;
         }
        
         button{
        width:6.35rem;
        height:1.925rem;
        background:orange;
        border:none;
        border-radius: 1rem 1rem 1rem 1rem;
         position:absolute;
        top:13.5rem;
        left:4rem;
      }
      }
   }
}


    

</style>



