      <div class="">
        <div class="wrap">
          <ul>
            <li @click="num1=true">
              <div class="img1">
                  <img src="../assets/radio.png" alt=""  v-if='!num1' >
                  <img src="../assets/radio2.png" alt="" v-if='num1'>
              </div>
