              <h2>很遗憾，正确答案是 B。</h2>
              <p>将首页地图缩小显示后，将会出现蓝色覆盖区域，该区域即使小蜜蜂的运营区域。只能在运营区域范围内骑行，否则会扣除相应的押金以及信用积分</p>
