<script>
export default {
  data(){
    return{
      num1:false,
      num2:false,
      num3:false,
      num4:false,
      num5:false,
      num6:false,
    }
  },
  methods:{
    lick(){
  		this.num4=this.num1;
  		this.num5=this.num2;
  		if(this.num1 == false && this.num2==false){
  			this.num6=!this.num6
  		}
  	}
  }
}
