        padding-top: 4rem;
        text-align: center;
      }
      .error>div>p{
        padding-top: 0.5rem;
        font-size: 0.5rem;
        text-align: center;
      }
      .error>div>a{
        width: 6.35rem;
        height: 1.6rem;
        background: orange;
        border: 0;
        border-radius:1rem;
        position: absolute;
        top: 9.5rem;
        left: 3rem;
        text-align: center;
        line-height: 1.6rem;
