<template>
	<div id="app">
		<!--<router-link to="/repeat">repeat</router-link>
    <router-link to="/red">red</router-link>-->
		<router-view></router-view>
	</div>
</template>

<script>
	(function(win, doc) {
		win.onresize = function() {
			change();
		};
		change();

		function change() {
			var oFs = doc.documentElement.clientWidth / (320 / 20);
			doc.documentElement.style.fontSize = oFs + 'px';
		}
	})(window, document);

	export default {
		name: 'app',

	}
</script>

<style>
	#app {}
</style>


